import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsComplexLocationExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-location/ps-complex-location.component.interface';
import { IOptionsPsComplexSelectBranchComponentExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-select-branch/ps-complex-select-branch.component.interface';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniCommonService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-common.service';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsContainerPanel, IOptionsPsKeyinInputExposed, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'supplementary-card-request',
  templateUrl: './supplementary-card-request.page.html',
  styleUrls: ['./supplementary-card-request.page.scss'],
})
export class SupplementaryCardRequestPage extends OmniBasePage implements OnInit {
  private formGroup = new FormGroup({});
  supplementaryCardRequestVO = {};

  public stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'supplementary_card_request_stepper',
    numberOfSteps: 1,
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.cardActionRequestEndPoint,
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.updateScreenVO());
          });
        },
        executionClass: this,
        params: [this]
      },
      group: this.formGroup,
    },
    requestObject: this.supplementaryCardRequestVO,
    isHorizontalStepper: true,
    namesofSteps: ['cardManagement']
  };

  mainCardPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'main_card_information_key',
    iconName: 'cards',
    expanded: true
  };
  supplementaryCardPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'supplementary_card_key',
    iconName: 'cards',
    expanded: true
  };
  mapBranchPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'select_map_branch_key',
    iconName: 'location-pin',
    expanded: true
  };

  cifBranchPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'request_branch_key',
    iconName: 'location-pin',
    expanded: true
  };

  cifrequestBranchOptions: IOptionsPsInputVarcharExposed = {
    placeHolder: 'request_branch_key',
    labelKey: 'request_branch_key',
    group: this.formGroup,
    fcName: 'branch',
  }

  cardHolderNameOptions: IOptionsPsInputVarcharExposed = {
    placeHolder: 'cardholder_name_key',
    labelKey: 'cardholder_name_key',
    group: this.formGroup,
    fcName: 'cardHolderName',
  };

  deliveryAddressDetails: IOptionsPsComplexLocationExposed = {
    collectionModeOptions: {
      group: this.formGroup,
      fcName: 'deliveryLoc',
    },
    branchLocationOptions: {
      fcName: 'destinationBranchesDropdown',
      group: this.formGroup,
      countryRegionCityOptions: {
        countriesOptions: {
          fcName: 'destinationBranchCountry'
        },
        regionOptions: {
          fcName: 'destinationBranchRegion'
        },
        cityOptions: {
          fcName: 'destinationBranchCity'
        }
      },
      branchesOptions: {
        fcName: 'destinationBranch'
      }
    },
    homeAddressOptions: {
      placeHolder: 'home_address_key',
      labelKey: 'home_address_key',
      group: this.formGroup,
      fcName: 'homeAddress',
      requestVO: this.supplementaryCardRequestVO,
    },
    workAddressOptions: {
      placeHolder: 'work_address_key',
      labelKey: 'work_address_key',
      group: this.formGroup,
      fcName: 'workAddress',
      requestVO: this.supplementaryCardRequestVO,
    },
    otherAddressOptions: {
      labelKey: 'address_details_key',
      rows: '3',
      cols: '10',
      fcName: 'otherAddressDetails',
      group: this.formGroup,
    },
    branchCifOptions: {
      placeHolder: 'request_branch_key',
      labelKey: 'request_branch_key',
      group: this.formGroup,
      fcName: 'branchName',
    },
    cifBranchChecking: true

  };

  selectBranchOptions: IOptionsPsComplexSelectBranchComponentExposed = {
    fcName: 'mapAtmBranchesDropdown',
    group: this.formGroup,
  };

  itemCard: any = {};
  cardOptions: IOptionsPsContainerLookupOptionComponentExposed = {};

  constructor(
    private navService: PsNavigatorService,
    public commonProv: PsCommonService,
    private omniPull: OmniPullService,
    public loggerP: LoggerService,
    public omniCommon: OmniCommonService,
  ) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    //this.supplementaryCardRequestVO = this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {};
    this.itemCard = this.supplementaryCardRequestVO;
    this.cardOptions.formGroup = this.formGroup;
    this.cardOptions.labelsValueMap = this.navigationServices.getParamKey('labelsValueMap');
    this.cardOptions.headerMap = this.navigationServices.getParamKey('headerMap');
    this.cardOptions.showInitialCardValues = this.navigationServices.getParamKey('showInitialCardValues');
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.fetchParameter();
  }

  async fetchParameter() {
    const cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);

    const result = await this.omniPull.getParamValOf(CommonBussinessConstant.CARDS_REQUEST_BRANCH)
      .catch((error) => {
        this.loggerP.error(error);
      });

    if (result !== -1 && result.RequestBranch === CommonBussinessConstant.END_USER_INPUT_ID) {
      this.omniCommon.branchSelectionAndMapControl(0, [{ fcName: 'cif_branch_panel' }]);
      this.omniCommon.branchSelectionAndMapControl(1, [{ fcName: 'branch_map_panel' }]);
    } else {
      if (result.RequestBranch === CommonBussinessConstant.ACCOUNT_BRANCH_ID) {
        this.cifrequestBranchOptions.labelKey = "account_branch_key";
        this.commonProv.setValInsideNestedObj(this.cifrequestBranchOptions.fcName, cifInfo.cifBranch ? cifInfo.customerInfoCO.branchCode : '', this.supplementaryCardRequestVO);
      } else {
        this.cifrequestBranchOptions.labelKey = "cif_branch_key";
        this.commonProv.setValInsideNestedObj(this.cifrequestBranchOptions.fcName, cifInfo.cifBranch ? cifInfo.customerInfoCO.branchBriefDesc : '', this.supplementaryCardRequestVO);
      }
      this.omniCommon.branchSelectionAndMapControl(1, [{ fcName: 'cif_branch_panel' }]);
      this.omniCommon.branchSelectionAndMapControl(0, [{ fcName: 'branch_map_panel' }]);
    }
  }

  onSelectBranch(event) {
    if (event.itemValue !== undefined) {
      this.formGroup.controls[this.selectBranchOptions.fcName].setValue(event);
      this.commonProv.setValInsideNestedObj(this.selectBranchOptions.fcName, event.selectedObj, this.supplementaryCardRequestVO);
    }
  }
  updateScreenVO() {
    this.supplementaryCardRequestVO['primarySupplementary'] = 'Supplementary';
    this.supplementaryCardRequestVO['primaryCard'] = this.supplementaryCardRequestVO['cardNumber'];
    delete this.supplementaryCardRequestVO['ownerName'];
    delete this.supplementaryCardRequestVO['key'];
  }

}
